@charset "utf-8";

*{margin: 0px;padding: 0px;}
ul,ol,li{list-style: none;}
.slide-wrapper{
  width: 100%;
  height: 370px;
  margin: 0px auto;
  position: relative;
  overflow: hidden;
}
.slide{
  display: flex;
  width: 1920px;
  position: absolute;
  left: 0px; top: 0px;
  transition: 0.7s;
}
.slide > li > img{
  width: 1920px;
}

.btn_group > input[type=button]{
  position: absolute;
  color: rgb(255, 255, 255);
  opacity: 0.2;
}
.btn_group .prev{
  left: 20%;
  padding: 20px 27px 20px 27px; 
  margin-top: 140px;
  background-color: rgb(0, 0, 0);  

  border:none;
  border-radius: 100%;
  font-size: 20px;
  
}
.btn_group .next{
  right: 20%;
  padding: 20px 27px 20px 27px; 
  margin-top: 140px;
  background-color: rgb(0, 0, 0);
  border:none;
  border-radius: 100%;
  font-size: 20px;
}
.slide li img:hover .btn_group>input[type=button]{
  opacity: 0.2;
}